/* eslint-disable @typescript-eslint/no-explicit-any */
"use client"

import { useEffect, useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Badge } from "@/components/ui/badge"
import { Skeleton } from "@/components/ui/skeleton"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Collapsible, CollapsibleContent, CollapsibleTrigger } from "@/components/ui/collapsible"
import {
  AlertCircle,
  CheckCircle2,
  Save,
  Users,
  FileText,
  PresentationIcon,
  MessageCircle,
  Brain,
  ChevronDown,
  ChevronUp,
} from "lucide-react"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"

interface StudentGroup {
  idGroupe: number
  nomGroupe: string
  idEtudiant: number
  etudiant: string
  idSoutenance: number
  noteRapport?: number
  notePresentation?: number
  noteDiscussion?: number
  noteSavoirFaireSavoirEtre?: number
  noteTotale?: number
  remarque?: string
}

export default function EvaluationPage() {
  const [students, setStudents] = useState<StudentGroup[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [saving, setSaving] = useState(false)
  const [successMessage, setSuccessMessage] = useState<string | null>(null)
  const [validationErrors, setValidationErrors] = useState<Record<string, string>>({})
  const [openRows, setOpenRows] = useState<Record<number, boolean>>({})
  const idEncadrant = 1 // Should be dynamic (e.g., from auth context)

  useEffect(() => {
    async function fetchStudents() {
      try {
        const res = await fetch(`http://localhost:5000/api/groups-students/${idEncadrant}`)
        if (!res.ok) {
          const errorData = await res.json()
          throw new Error(errorData.error || "Erreur lors du chargement des données")
        }
        const data: StudentGroup[] = await res.json()

        // Ensure idSoutenance exists for each student
        if (data.some((student) => !student.idSoutenance)) {
          throw new Error("Some students are missing idSoutenance from the backend")
        }

        // Load data with calculated totals, keeping existing grades
        const studentsWithTotal = data.map((student) => ({
          ...student,
          noteTotale: calculateTotal(student),
        }))

        setStudents(studentsWithTotal)
      } catch (error: any) {
        setError(error.message)
      } finally {
        setLoading(false)
      }
    }
    fetchStudents()
  }, [idEncadrant])

  const calculateTotal = (student: StudentGroup): number | undefined => {
    const { noteRapport, notePresentation, noteDiscussion, noteSavoirFaireSavoirEtre } = student
    const grades = [noteRapport, notePresentation, noteDiscussion, noteSavoirFaireSavoirEtre].filter(
      (grade) => grade !== undefined && grade !== null && !isNaN(grade),
    ) as number[]

    if (grades.length === 0) return undefined

    const sum = grades.reduce((acc, grade) => acc + grade, 0)
    return Number((sum / grades.length).toFixed(2))
  }

  const validateGrade = (value: string, index: number, field: string): boolean => {
    const numValue = Number(value)
    const errorKey = `${index}-${field}`

    if (value && (isNaN(numValue) || numValue < 0 || numValue > 20)) {
      setValidationErrors((prev) => ({
        ...prev,
        [errorKey]: "La note doit être entre 0 et 20",
      }))
      return false
    } else {
      setValidationErrors((prev) => {
        const newErrors = { ...prev }
        delete newErrors[errorKey]
        return newErrors
      })
      return true
    }
  }

  const handleInputChange = (index: number, field: string, value: string | number) => {
    const gradeFields = ["noteRapport", "notePresentation", "noteDiscussion", "noteSavoirFaireSavoirEtre"]
    if (gradeFields.includes(field)) {
      const isValid = validateGrade(value.toString(), index, field)
      if (!isValid && typeof value === "string") {
        const updatedStudents = [...students]
        ;(updatedStudents[index] as any)[field] = value // Allow invalid input temporarily for display
        setStudents(updatedStudents)
        return
      }
    }

    const updatedStudents = [...students]
    const parsedValue = field === "remarque" ? value : value === "" ? undefined : Number(value)
    ;(updatedStudents[index] as any)[field] = parsedValue

    if (gradeFields.includes(field)) {
      updatedStudents[index].noteTotale = calculateTotal(updatedStudents[index])
    }

    setStudents(updatedStudents)
  }

  const handleSubmit = async () => {
    if (saving || Object.keys(validationErrors).length > 0) return

    setSaving(true)
    setSuccessMessage(null)
    setError(null)

    try {
      // Filter students with at least one grade or remark
      const evaluations = students
        .filter((student) => {
          return (
            student.noteRapport !== undefined ||
            student.notePresentation !== undefined ||
            student.noteDiscussion !== undefined ||
            student.noteSavoirFaireSavoirEtre !== undefined ||
            (student.remarque && student.remarque.trim() !== "")
          )
        })
        .map((student) => ({
          idSoutenance: student.idSoutenance,
          idGroupe: student.idGroupe,
          idEtudiant: student.idEtudiant,
          noteRapport: student.noteRapport,
          notePresentation: student.notePresentation,
          noteDiscussion: student.noteDiscussion,
          noteSavoirFaireSavoirEtre: student.noteSavoirFaireSavoirEtre,
          remarque: student.remarque,
        }))

      if (evaluations.length === 0) {
        throw new Error("Aucune évaluation à enregistrer - ajoutez au moins une note ou remarque")
      }

      const res = await fetch("http://localhost:5000/api/evaluations", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ idEncadrant, evaluations }),
      })

      if (!res.ok) {
        const errorData = await res.json()
        throw new Error(errorData.error || "Erreur lors de l'enregistrement")
      }

      const result = await res.json()
      setSuccessMessage(result.message || "Évaluations enregistrées avec succès!")
      window.scrollTo({ top: 0, behavior: "smooth" })

      // Keep the current state (with updated grades) instead of resetting
      // Optionally refresh to confirm saved data
      const refreshedRes = await fetch(`http://localhost:5000/api/groups-students/${idEncadrant}`)
      const refreshedData: StudentGroup[] = await refreshedRes.json()
      setStudents(
        refreshedData.map((student) => ({
          ...student,
          noteTotale: calculateTotal(student),
        })),
      )
    } catch (error: any) {
      setError(error.message)
      window.scrollTo({ top: 0, behavior: "smooth" })
    } finally {
      setSaving(false)
    }
  }

  const getGradeColor = (grade?: number): string => {
    if (grade === undefined) return ""
    if (grade < 8) return "text-red-600"
    if (grade < 12) return "text-orange-600"
    if (grade < 16) return "text-blue-600"
    return "text-green-600"
  }

  const getGradeBgColor = (grade?: number): string => {
    if (grade === undefined) return ""
    if (grade < 8) return "bg-red-50"
    if (grade < 12) return "bg-orange-50"
    if (grade < 16) return "bg-blue-50"
    return "bg-green-50"
  }

  const toggleRow = (index: number) => {
    setOpenRows((prev) => ({
      ...prev,
      [index]: !prev[index],
    }))
  }

  if (loading) {
    return (
      <div className="max-w-7xl mx-auto p-4 md:p-6">
        <Card className="border shadow-md">
          <CardHeader className="pb-4 border-b bg-slate-50">
            <Skeleton className="h-8 w-64 mb-2" />
            <Skeleton className="h-4 w-full" />
          </CardHeader>
          <CardContent className="p-6">
            <Skeleton className="h-96 w-full" />
          </CardContent>
        </Card>
      </div>
    )
  }

  return (
    <div className="max-w-7xl mx-auto p-4 md:p-6">
      <Card className="border shadow-md overflow-hidden">
        <CardHeader className="pb-4 border-b bg-gradient-to-r from-blue-50 to-indigo-50">
          <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-2">
            <div>
              <CardTitle className="text-2xl font-bold text-slate-800">Évaluation des étudiants</CardTitle>
              <CardDescription className="text-slate-600">
                Attribuez des notes entre 0 et 20 pour chaque critère d'évaluation
              </CardDescription>
            </div>
            <Badge
              variant="outline"
              className="bg-white text-blue-600 border-blue-200 px-3 py-1 font-medium flex items-center gap-1"
            >
              <Users className="h-3.5 w-3.5" />
              {students.length} Étudiants
            </Badge>
          </div>
        </CardHeader>

        <CardContent className="p-0">
          {successMessage && (
            <Alert className="m-6 bg-green-50 text-green-800 border-green-200">
              <CheckCircle2 className="h-4 w-4 text-green-600" />
              <AlertTitle>Succès</AlertTitle>
              <AlertDescription>{successMessage}</AlertDescription>
            </Alert>
          )}

          {error && (
            <Alert className="m-6 bg-red-50 text-red-800 border-red-200">
              <AlertCircle className="h-4 w-4 text-red-600" />
              <AlertTitle>Erreur</AlertTitle>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {students.length === 0 ? (
            <div className="flex flex-col items-center justify-center py-12">
              <Users className="h-12 w-12 text-slate-300 mb-4" />
              <p className="text-slate-500 text-center">Aucun étudiant trouvé.</p>
            </div>
          ) : (
            <div className="overflow-x-auto">
              <Table>
                <TableHeader className="bg-slate-50">
                  <TableRow>
                    <TableHead className="w-[250px]">Étudiant</TableHead>
                    <TableHead className="text-center">
                      <div className="flex items-center justify-center gap-1">
                        <FileText className="h-4 w-4 text-blue-600" />
                        <span>Rapport</span>
                      </div>
                    </TableHead>
                    <TableHead className="text-center">
                      <div className="flex items-center justify-center gap-1">
                        <PresentationIcon className="h-4 w-4 text-amber-600" />
                        <span>Présentation</span>
                      </div>
                    </TableHead>
                    <TableHead className="text-center">
                      <div className="flex items-center justify-center gap-1">
                        <MessageCircle className="h-4 w-4 text-green-600" />
                        <span>Discussion</span>
                      </div>
                    </TableHead>
                    <TableHead className="text-center">
                      <div className="flex items-center justify-center gap-1">
                        <Brain className="h-4 w-4 text-purple-600" />
                        <span>Savoir-faire</span>
                      </div>
                    </TableHead>
                    <TableHead className="text-center">Note Totale</TableHead>
                    <TableHead className="text-center">Remarque</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {students.map((student, index) => (
                    <Collapsible
                      key={`${student.idSoutenance}-${student.idGroupe}-${student.idEtudiant}`}
                      open={openRows[index]}
                      onOpenChange={() => {}}
                    >
                      <TableRow className="hover:bg-slate-50">
                        <TableCell className="font-medium">
                          <div>
                            <div className="font-medium">{student.etudiant}</div>
                            <div className="text-sm text-slate-500">Groupe: {student.nomGroupe}</div>
                          </div>
                        </TableCell>
                        <TableCell className="text-center">
                          <Input
                            type="number"
                            min="0"
                            max="20"
                            step="0.01"
                            value={student.noteRapport !== undefined ? student.noteRapport : ""}
                            onChange={(e) => handleInputChange(index, "noteRapport", e.target.value)}
                            className={`w-20 mx-auto text-center ${validationErrors[`${index}-noteRapport`] ? "border-red-500 focus-visible:ring-red-500" : ""}`}
                          />
                          {validationErrors[`${index}-noteRapport`] && (
                            <p className="text-red-500 text-xs mt-1 text-center">
                              {validationErrors[`${index}-noteRapport`]}
                            </p>
                          )}
                        </TableCell>
                        <TableCell className="text-center">
                          <Input
                            type="number"
                            min="0"
                            max="20"
                            step="0.01"
                            value={student.notePresentation !== undefined ? student.notePresentation : ""}
                            onChange={(e) => handleInputChange(index, "notePresentation", e.target.value)}
                            className={`w-20 mx-auto text-center ${validationErrors[`${index}-notePresentation`] ? "border-red-500 focus-visible:ring-red-500" : ""}`}
                          />
                          {validationErrors[`${index}-notePresentation`] && (
                            <p className="text-red-500 text-xs mt-1 text-center">
                              {validationErrors[`${index}-notePresentation`]}
                            </p>
                          )}
                        </TableCell>
                        <TableCell className="text-center">
                          <Input
                            type="number"
                            min="0"
                            max="20"
                            step="0.01"
                            value={student.noteDiscussion !== undefined ? student.noteDiscussion : ""}
                            onChange={(e) => handleInputChange(index, "noteDiscussion", e.target.value)}
                            className={`w-20 mx-auto text-center ${validationErrors[`${index}-noteDiscussion`] ? "border-red-500 focus-visible:ring-red-500" : ""}`}
                          />
                          {validationErrors[`${index}-noteDiscussion`] && (
                            <p className="text-red-500 text-xs mt-1 text-center">
                              {validationErrors[`${index}-noteDiscussion`]}
                            </p>
                          )}
                        </TableCell>
                        <TableCell className="text-center">
                          <Input
                            type="number"
                            min="0"
                            max="20"
                            step="0.01"
                            value={
                              student.noteSavoirFaireSavoirEtre !== undefined ? student.noteSavoirFaireSavoirEtre : ""
                            }
                            onChange={(e) => handleInputChange(index, "noteSavoirFaireSavoirEtre", e.target.value)}
                            className={`w-20 mx-auto text-center ${validationErrors[`${index}-noteSavoirFaireSavoirEtre`] ? "border-red-500 focus-visible:ring-red-500" : ""}`}
                          />
                          {validationErrors[`${index}-noteSavoirFaireSavoirEtre`] && (
                            <p className="text-red-500 text-xs mt-1 text-center">
                              {validationErrors[`${index}-noteSavoirFaireSavoirEtre`]}
                            </p>
                          )}
                        </TableCell>
                        <TableCell className="text-center">
                          <div
                            className={`px-3 py-1.5 rounded-md font-medium inline-block min-w-[60px] ${getGradeBgColor(student.noteTotale)} ${getGradeColor(student.noteTotale)}`}
                          >
                            {student.noteTotale !== undefined && !isNaN(student.noteTotale) ? student.noteTotale : "-"}
                            /20
                          </div>
                        </TableCell>
                        <TableCell>
                          <CollapsibleTrigger asChild onClick={() => toggleRow(index)}>
                            <Button variant="ghost" size="sm" className="h-8 w-8 p-0">
                              {openRows[index] ? (
                                <ChevronUp className="h-4 w-4" />
                              ) : (
                                <ChevronDown className="h-4 w-4" />
                              )}
                              <span className="sr-only">Toggle remarks</span>
                            </Button>
                          </CollapsibleTrigger>
                        </TableCell>
                      </TableRow>
                      <CollapsibleContent>
                        <TableRow className="bg-slate-50 border-t-0">
                          <TableCell colSpan={7} className="p-4">
                            <div>
                              <div className="text-sm font-medium text-slate-700 mb-2">Remarque</div>
                              <Textarea
                                value={student.remarque ?? ""}
                                onChange={(e) => handleInputChange(index, "remarque", e.target.value)}
                                placeholder="Commentaires sur la performance de l'étudiant..."
                                className="resize-none w-full"
                                rows={2}
                              />
                            </div>
                          </TableCell>
                        </TableRow>
                      </CollapsibleContent>
                    </Collapsible>
                  ))}
                </TableBody>
              </Table>
            </div>
          )}
        </CardContent>

        <CardFooter className="p-6 bg-slate-50 border-t flex justify-end">
          <Button
            onClick={handleSubmit}
            disabled={saving || Object.keys(validationErrors).length > 0}
            className="px-6 py-2 bg-blue-600 hover:bg-blue-700 text-white"
            size="lg"
          >
            {saving ? (
              <>
                <span className="mr-2">Enregistrement...</span>
                <div className="h-4 w-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
              </>
            ) : (
              <>
                <Save className="mr-2 h-4 w-4" />
                Enregistrer les évaluations
              </>
            )}
          </Button>
        </CardFooter>
      </Card>
    </div>
  )
}

